import React, { Component } from 'react';
import './App.css';
import './css/TakeOrder.css'
import Select from 'react-select';
import '../node_modules/react-select/dist/react-select.css';
import axios from 'axios';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import RaisedButton from 'material-ui/RaisedButton';
import moment from 'moment';
import DatePicker from 'material-ui/DatePicker';
import CircularProgress from 'material-ui/CircularProgress';
import Snackbar from 'material-ui/Snackbar';
import IconButton from 'material-ui/IconButton';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import {initYolo} from './YoloModule'
import ItemRow from './ItemRowComponent';
import config from './config'

var products = [];


class TakeOrder extends Component {
  constructor(props) {
    super(props);

  //Set delivery date
  let deliveryDate  = new Date(); 
  if (deliveryDate.getHours() > 2) {
    deliveryDate.setDate(deliveryDate.getDate() + 1);
  }

  deliveryDate.setHours(0, 0, 0, 0);

  
    this.state = {
      order : [
        {sid:0, quantity: 0, price : 0 }
      ],
      selectedOption: '',
      customers: [
            { value: 'one', label: 'Data not received' }
          ], 
      isLoggedIn: false,
      orderOpen: false,
      open: false,
      deliveryDate: deliveryDate,
      disableOrder : false
    };

  this.handleChange = this.handleChange.bind(this);
  this.handleKeyPress = this.handleKeyPress.bind(this);
  this.handleDeliveryDate = this.handleDeliveryDate.bind(this);
  this.sendOrder = this.sendOrder.bind(this);
  this.clearOrder = this.clearOrder.bind(this);
  this.initOrders = this.initOrders.bind(this);
  this.getName = this.getName.bind(this);
  this.getOutstanding = this.getOutstanding.bind(this);
  this.getHistory = this.getHistory.bind(this);

  }

  initOrders() { 

  var headers =  { } ;
  var token = localStorage.getItem('token');

  headers.Authorization = "Bearer " + token ;
  var promise1 = axios({
                  method : 'GET',
//                  url : 'api/crops.json',
                  url: config.apiserver + '/v1/api/crops/',
                  headers : headers
                      }).then(response =>  {      
                  products = response.data.data; 
                  for (var i=0; i< products.length; i++) {
                     products[i].sid = i+1;
                  }
                  this.forceUpdate() ;
                  console.log(products);
    });

  var promise2 =axios({
        method : 'GET',
        url: config.apiserver + '/v1/api/customers/',
       // url : 'api/customers.json',
        headers : headers
         }).then(response =>  {
              var resp = response.data.data;             
              var customers = [];
              this.setState({
                listCustomers: resp
              })

              for (var i=0; i< resp.length; i++) {
                customers[i] = {};
                //customers[i].value = resp[i].customerID;
                customers[i].value = resp[i].customerID + "-" + resp[i].customerName + "-" + resp[i].cusomerCity  + "-" + resp[i].locality ; 
                customers[i].label = resp[i].customerID + "-" + resp[i].customerName + "-" + resp[i].cusomerCity  + "-" + resp[i].locality ; 
              }
              
            this.setState({
              customers : customers
            });
      });

    Promise.all([promise1, promise2]).then(() => {
        this.setState({
          isLoggedIn: true
        });
    });

       }

  getCustomer(selected) {
    if (typeof(selected) == "object") {
    let cid = selected.label.split("-")[0]
    for (var i=0;i<this.state.listCustomers.length;i++) {
      if (this.state.listCustomers[i].customerID == cid) {
        return this.state.listCustomers[i]; 
      }
    }
  }
    return "NA";
  }

  getOutstanding() {

    return this.getCustomer(this.state.selectedOption).outstanding;
  }

  getHistory() {
    if (typeof(this.state.selectedOption) == "object") {
      return this.getCustomer(this.state.selectedOption).history; 
    } else {
      return [];
    }
  }

  componentWillMount() {
    initYolo(this.initOrders);
  }


  getName(sid) {
    for (var i=0; i< products.length; i++) {
      if (products[i].sid === sid) {
          return products[i].cropName;
      }
    }

    return "NA";
  }

  sendOrder() {

      if (typeof(this.state.selectedOption) != "object")  {
        alert("Please select customer");
        return;
      }

      this.setState({
          orderOpen: true,
          disableOrder: true
       });

       let today = moment().format('DD/MM/YYYY');

       let deliveryDate = moment(this.state.deliveryDate).format('DD/MM/YYYY');

       var data = {
              "customerTag" : "customerTag", 
              "orderStatus" : "Confirmed", 
              "orderDate" :  today, 
              "deliverDate": deliveryDate, 
              "orderData" : []
          };

        var orderData = [];

        for (var i=0; i<this.state.order.length; i++) {
          orderData[i] = {};
          orderData[i].cropName = this.getName(this.state.order[i].sid);
          orderData[i].quantity = this.state.order[i].quantity;
          orderData[i].price  = this.state.order[i].price;
        }

        data.customerTag = this.state.selectedOption.label;
        data.orderData = orderData;


        var headers =  { } ;
        var token = localStorage.getItem('token');

        headers.Authorization = "Bearer " + token ;
      var promise3 = axios({
        method: 'post',
        url: config.apiserver + '/v1/api/orders/',
        data: data,
        headers: headers
      }).then((response) => {
        alert('Order Created successfully');
        this.setState({
            disableOrder: false
         });        
        }).catch((error) => {
          alert("Please refresh the page and try again. If it persists, please contact Madhu or Bharath");
          console.log(error);
        this.setState({
            disableOrder: false
         });        
        });


    }


  clearOrder() {

    this.setState({
        order: [
         {sid: 0, quantity: 0, price : 0 }
        ],
       selectedOption:''
    })
  }

  handleSelectChange = (selectedOption) => {
    if (selectedOption == null ) {
      selectedOption = "";
    }
    this.setState({ selectedOption });
    console.log(selectedOption);
  }


  handleOpen = () => {
    this.setState({open: true});
  };

  handleClose = () => {
    this.setState({open: false});
  };


  handleChange(key, target) {
    const name = target.name;
    const value = target.value;
     var forder = this.state.order ; 
     console.log("key is" + key + " and target value  " + target.name);


     for (var i=0;i< forder.length;i++) {
       if (i=== key) {
          if (target.name === "delete") {
              forder.splice(i, 1);
          } else if (target.name === "sid" & value != NaN) {
            forder[i].sid = parseInt(value, 10);            
          } else {
            forder[i][name] = parseFloat(value, 10);
          }
          break;
       }
     }

    this.setState({
      order : forder
    })
  }


  handleKeyPress(sid) {
    console.log(this);
     var forder = this.state.order ; 
     for (var i=0;i< forder.length;i++) {
       if (forder[i].sid === sid) {
          //forder[i]. = parseInt(val);
          forder.splice(i + 1, 0, {sid: 0, quantity: 0, price : 0 });
          break;
       }
     }

    this.setState({
      order : forder
    });

  }

  handleDeliveryDate (event, date) {
    console.log("hello");
    console.log(date);
    this.setState({
      deliveryDate : date
    })

  }

  getTotal() {
    var total = 0;
    for (var i=0; i < this.state.order.length; i++) {
        total  += this.state.order[i].price*this.state.order[i].quantity; 
    }
    return total.toFixed(2)+ " INR" ;
  }

  formatNumber(num) {
          var n1, n2;
          num = num + '' || '';
          // works for integer and floating as well
          n1 = num.split('.');
          n2 = n1[1] || null;
          n1 = n1[0].replace(/(\d)(?=(\d\d)+\d$)/g, "$1,");
          num = n2 ? n1 + '.' + n2 : n1;
          return num;
  }

  render() {

  const rows = [];

  this.state.order.forEach((item, index) => {
      rows.push(
        <ItemRow
          index={index}
          serial={index + 1}
          sid={item.sid}
          quantity={item.quantity}
          price={item.price} 
          key={index} 
          onChange={this.handleChange}
          onKeyPress={this.handleKeyPress}
          getName={this.getName}
          />
      );
  });



  const { selectedOption } = this.state;
  const { customers } = this.state; 

    const styles = {
      radioButton: {
        marginTop: 16,
      },
    };

    const actions = [
      <FlatButton
        label="Close"
        primary={true}
        onClick={this.handleClose}
      />,
    ];


    const productRows = []
    for (let i=0; i< products.length; i++) {
      productRows.push(
        <div key={i}
        ><span>{products[i].sid}</span>-<span>{products[i].cropName}</span></div>
        )
    };


    const historyRows = []

    for(let i=0;i< this.getHistory().length;i++) {

      let index = Object.keys(this.getHistory()[i])[0];
      let order = this.formatNumber(this.getHistory()[i][index].order) ; 
      let payment = this.formatNumber(this.getHistory()[i][index].payment) ; 

      let indate  = index.split("/");
      indate = indate[1] + '/' + indate[0] +  "/" + indate[2];
      let date = moment(indate).format("Do MMM");
      historyRows.push(
          <tr key={i} >
            <td>{date}</td>
            <td>{order}</td>
            <td>{payment}</td>
          </tr>
        )
    }



    if (!this.state.isLoggedIn) {
      return (
        
        <div className="loading-box">
          <CircularProgress size={60} thickness={7} />
        </div>
                )
    }

    return (
      <div className="App">
       
  <div className="main">
  <h1>Add Order</h1>
  <div className="screens">
  <div className="order-box"> 
    <div className="customer-select-box">
      <span>Customer: </span>
      <Select
        className="customer-select"
        name="form-field-name"
        value={selectedOption}
        onChange={this.handleSelectChange}
        options={customers}
      />
       <IconButton
        iconClassName="material-icons"
        tooltip="Product list"
        onClick={this.handleOpen} 
        >
      info
    </IconButton>
    </div>
    <div>
    Delivery Date : 
    <DatePicker hintText="Select Date"
          container="inline"
          value={this.state.deliveryDate} 
          onChange={this.handleDeliveryDate}          
          />
    </div>
        <table className="order-table">
        <tbody>
          <tr>
            <td>S. No</td>
            <td>Bono Code</td>
            <td>Name</td>
            <td>Quantity(Kg)</td>
            <td>Price(Rs)</td>
          </tr>
          {rows}
        <tr className="grand-total">
          <td colSpan="4">Grand Total</td>
          <td>{this.getTotal()}</td>
        </tr>
        </tbody>
        </table>
        <RaisedButton 
            className="confirm-order"
            label="Reset"
            primary={true}
            onClick={this.clearOrder}    

             />
        <RaisedButton 
            className="confirm-order order-button"
            label="Order"
            primary={true}
            onClick={this.sendOrder}   
            disabled={this.state.disableOrder}         
             />
        <Snackbar
          open={this.state.orderOpen}
          message="Order is getting created ..."
          autoHideDuration={4000}
          onRequestClose={this.handleRequestClose}
        />
        <Dialog
          title="List of Products"
          actions={actions}
          modal={false}
          open={this.state.open}
          onRequestClose={this.handleClose}
          autoScrollBodyContent={true}
        >
        {productRows}
        </Dialog>
        </div>
          <div className="customer-history">
              <div>Outstanding amount: <span>{this.getOutstanding()} INR</span></div>
              <table className="history-table">
              <tbody>
                <tr>
                  <td>Date</td>
                  <td>Order</td>
                  <td>Payment</td>
                </tr>
                {historyRows}
              </tbody>
              </table>
          </div>
        </div>
            </div>
      </div>
    );
  }
}

export default TakeOrder;



