import React, { Component } from 'react';
import './App.css';
import Select from 'react-select';
import '../node_modules/react-select/dist/react-select.css';
import axios from 'axios';
import config from './config'
import moment from 'moment';
import DatePicker from 'material-ui/DatePicker';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import Snackbar from 'material-ui/Snackbar';
import {initYolo} from './YoloModule'
import CircularProgress from 'material-ui/CircularProgress';

class PaymentEntry extends Component {

  constructor(props) {
    super(props);
  //Set payment date
  let paymentDate  = new Date(); 
  paymentDate.setHours(0, 0, 0, 0);


    this.state = {
      selectedOption: '',
      customers: [
            { value: 'one', label: 'Data not received' }
          ],
       paymentDate: paymentDate,
       paymentMode: "",
       amount: '',
       snackbarOpen: false,
       isLoggedIn: false
    };

      this.handlePaymentDate = this.handlePaymentDate.bind(this);
      this.handlePaymentModeChange = this.handlePaymentModeChange.bind(this);
      this.handlePaymentAmount = this.handlePaymentAmount.bind(this);
      this.postPayment = this.postPayment.bind(this);
      this.initPayment = this.initPayment.bind(this);
  }


  initPayment() { 

  var headers =  { } ;
  var token = localStorage.getItem('token');

  headers.Authorization = "Bearer " + token ;


  var promise2 =axios({
        method : 'GET',
         url: config.apiserver + '/v1/api/customers/',
        headers : headers
         }).then(response =>  {
              var resp = response.data.data;             
              var customers = [];
              for (var i=0; i< resp.length; i++) {
                customers[i] = {};
                //customers[i].value = resp[i].customerID;
                customers[i].value = resp[i].customerID + "-" + resp[i].customerName + "-" + resp[i].cusomerCity  + "-" + resp[i].locality ; 
                customers[i].label = resp[i].customerID + "-" + resp[i].customerName + "-" + resp[i].cusomerCity  + "-" + resp[i].locality ; 
              }
              
            this.setState({
              customers : customers, 
              isLoggedIn: true
            });
      });


       }

  


  componentWillMount() {
    initYolo(this.initPayment);
  }



  handleSelectChange = (selectedOption) => {
    this.setState({ selectedOption });
    console.log(`Selected: ${selectedOption.label}`);
  }


  handlePaymentDate (event, date) {

  	let now = new Date();
	var timeDiff = now.getTime() - date.getTime();
	var diffDays = timeDiff / (1000 * 3600 * 24); 
	console.log(diffDays);

	if (diffDays < 2 && diffDays > 0) {
	    this.setState({
	      paymentDate : date
	    })
	} else {
		alert("Only Today and Yesterday's payments are valid");		
	}
  }  


  handlePaymentModeChange (event, index, value)  {
  	this.setState({paymentMode: value});
  }


  handlePaymentAmount(event) {
  	console.log(event.target.value);
  	this.setState({amount: event.target.value});
  }


  postPayment() {

      if (this.state.selectedOption == '' || this.state.paymentMode == '' || this.state.amount == '') {

        alert("Please fill all the fields");
        return;
      }
      var self = this;
      this.setState({
          snackbarOpen: true
       });


       let paymentDate = moment(this.state.paymentDate).format('DD/MM/YYYY');

       var data = {
              "paymentDate": paymentDate, 
          };

        data.customerTag = this.state.selectedOption.label;
        data.paymentMode = this.state.paymentMode ; 
        data.amount = this.state.amount;

        var headers =  { } ;
        var token = localStorage.getItem('token');

        headers.Authorization = "Bearer " + token ;
      var promise3 = axios({
        method: 'post',
        url: config.apiserver + '/v1/api/payments/',
        data: data,
        headers: headers
      }).then((response) => {
        alert('Payment added successfully');
              this.setState({
               selectedOption: '',
               paymentMode: "",
               amount: '',
               snackbarOpen: false,
            })        
        }).catch(function (error) {
          alert("Please refresh the page and try again. If it persists, please contact Madhu or Bharath");
          console.log(error);
        });


    

  }
  
  render() {

    if (!this.state.isLoggedIn) {
      return (
        
        <div className="loading-box">
          <CircularProgress size={60} thickness={7} />
        </div>
                )
    }
    
    return (
    	  <div className="main">
	          <h1>Payment Entry</h1>
				<div className="customer-select-box">
				  <span>Customer: </span>
				  <Select
				    className="customer-select"
				    name="form-field-name"
				    value={this.state.selectedOption}
				    onChange={this.handleSelectChange}
				    options={this.state.customers}
				  />
				</div> 
				<div>
 				<DatePicker 
              floatingLabelText="Payment Date"
              hintText="Payment Date"
		          container="inline"
		          value={this.state.paymentDate} 
		          onChange={this.handlePaymentDate}          
		          />
		        </div>
				  <SelectField
			          floatingLabelText="Payment Mode"
			          value={this.state.paymentMode}
			          onChange={this.handlePaymentModeChange}
				   >
			          <MenuItem value="Cash"  primaryText="Cash" />
			          <MenuItem value="Paytm" primaryText="Paytm" />
			          <MenuItem value="Wire Transfer" primaryText="Wire Transfer" />
		        </SelectField>
		        <div>
			    <TextField
            floatingLabelText="Amount"
			      hintText="Enter mount"
			      type="number"
			      value={this.state.amount}
			      onChange={this.handlePaymentAmount}
			    />
			    </div>
        <RaisedButton 
            className="confirm-order"
            label="Confirm"
            primary={true}
            onClick={this.postPayment}           
             />
        <Snackbar
          open={this.state.snackbarOpen}
          message="Payment is getting added ..."
          autoHideDuration={4000}
          onRequestClose={this.handleRequestClose}
        />    
          </div>

    );
  }
}

export default PaymentEntry;


