import React, { Component } from 'react';
class ItemRow extends Component {

 

  constructor(props) {
    super(props);
  	this.handleChange = this.handleChange.bind(this);

  }

  handleChange(key, event) {
    this.props.onChange(key, event.target);
  }


  _handleKeyPress = (e, sid) => {
    if (e.key === 'Enter') {
      this.props.onKeyPress(sid);
      //alert("enter clicked");
    }
  }

  render() {

     var delEvent = { target: { name : "delete"}} ; 
    return (
          <tr className="data-row" >
            <td onClick={(e) => this.handleChange(this.props.index, delEvent) }
            >{this.props.serial}</td>
            <td><input value={this.props.sid} 
            onChange={(e) => this.handleChange(this.props.index, e) }
            type="number" name="sid" /></td>
            <td>{this.props.getName(this.props.sid)}</td>
            <td><input type="number" value={this.props.quantity} name="quantity" 
            onChange={(e) => this.handleChange(this.props.index, e)}  /></td>
            <td><input type="number" value={this.props.price} 
            onKeyPress={(e) => this._handleKeyPress(e, this.props.sid)}
            onChange={(e) => this.handleChange(this.props.index, e)}  name="price" /></td>
          </tr>
    );
  }
}

export default ItemRow;


